.App {
  text-align: center;
}

body {
  background-color: whitesmoke;
}



.Card:hover {
  transform: scale(1.05);
}

.buttonWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  margin: 1rem auto;
}

.button {
  padding: 0.75rem 1.25rem;
  border-radius: 10rem;
  width: 20%;
  min-width: 150px;
  color: #fff;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.15rem;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
  text-align: center;
  cursor: pointer;
  max-width: 25%;
  margin: 1rem auto;
}

.button:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: green;
  border-radius: 10rem;
  z-index: -2;
}

.button:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  /* filter: brightness(50%);
  background-color: darken(green, 15%); */
  transition: all 0.3s;
  border-radius: 10rem;
  z-index: -1;
}

.button:hover {
  color: rgb(149, 149, 245);
  box-shadow: 1px 1px 10px grey;
  }

  .button:hover:before {
    width: 100%;
  }

.button:active {
  color: rgb(0, 0, 172);
  box-shadow: 1px 1px 3px grey;
}

input {
  height: 2rem;
  width: 21%;
  min-width: 175px;
  margin-top: 1rem;
  border-radius: 2rem;
  padding: 3px;
  font-size: 1.1rem;
}

input:focus{
  outline: none;
}